.eui-messagebox{position:fixed;top:50%;left:50%;max-width:18.75rem;min-width:18.75rem;overflow:hidden;text-align:center;background:#fff;border-radius:.25rem;-webkit-transform:translate(-50%,-50%);transform:translate(-50%,-50%)}.eui-messagebox-close{position:absolute;top:.625rem;right:.625rem;z-index:1}.eui-messagebox-title{position:relative;margin:1.25rem;font-size:1rem;font-weight:700;line-height:1.6}.eui-messagebox-content{max-height:55vh;margin:1.25rem;overflow-x:hidden;font-size:.875rem;line-height:normal;color:#666}.eui-messagebox-line{background-image:-webkit-gradient(linear,left top,left bottom,from(#fff),to(#e5e5e5));background-image:linear-gradient(180deg,#fff,#e5e5e5);background-repeat:no-repeat;background-size:auto 1PX;height:1PX}.eui-messagebox-buttons{display:-webkit-box;display:-ms-flexbox;display:flex;-ms-flex-wrap:nowrap;flex-wrap:nowrap;-webkit-box-orient:horizontal;-webkit-box-direction:reverse;-ms-flex-direction:row-reverse;flex-direction:row-reverse}.eui-messagebox-buttons-multiline{display:block;padding:0 1.25rem;background-image:none}.eui-messagebox-buttons-multiline-last{color:#666;background:none;border:none}.eui-messagebox-buttons-multiline-single{padding-bottom:1.25rem}